.skills-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #3d3d3d;
    box-shadow:  inset 0px 11px 8px -10px,
    inset 0px -11px 8px -10px; 
    padding: 10px;
}
.skills-title {
    font-size: 45px;
    margin: 0;
    padding: 40px;
    color: white;
}

.skills-flex-container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 1200px;
    opacity: 0;
    gap: 15px;
    padding: 20px;
    position: relative;
}
.skills-title::after {
    content: '';
    position: absolute;
    bottom:0;
    width: 10%;
    height: 10px;
    border-radius: 50px;
    left: 50%;
    bottom: 30px;
    transform: translate(-50%); 
    background: #fbb72c;
}
.card-container {
    width: 300px;
    min-height: 390px;
    padding: 20px;
    background-color: #f3f4f5;
    text-align: left;
    box-shadow: 2px 2px 8px 2px;
}
.card-header {
    font-weight: 650;
    font-size: 18px;
    align-self: flex-start;
}
.card-description {
    font-size: 20px;
    line-height: 1.3em;
    color: #666;
}