.navbar-container{
  background: transparent;
  width: 100%;
  position: fixed;
  display: flex;
  z-index: 2;
  top: 0;
  justify-content: space-between;
  align-items: center;
  transition: all .2s ease-in-out;
  padding-bottom: 5px;
}
.h1-link {
  text-decoration: none;
}
.nav-header {
  margin-left: 20px;
  color: white;
  border-bottom: 2px solid white;
  transition: color .2s ease-out;
}
.nav-header:hover {
  color:#fbb72c;
  border-color: #fbb72c;
}
.active {
  background-color: black;
}
.middle-nav-div {
  display: flex;
  gap: 30px;
}
.middle-nav-div a {
  display: inline-block;
  position: relative;
  font-weight: 500;
  color: white;
  cursor: pointer;
  font-size: 15px;
  transition: scale .3s ease-in-out;
  text-decoration: none;
  color:white
}
.middle-nav-div a::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fbb72c;
  transform-origin: bottom;
  transition: transform 0.25s ease-out;
}
.middle-nav-div a:hover{
scale: 1.2;
}
.middle-nav-div a:hover::after{
  transform: scaleX(1);
  transform-origin: bottom;
}

.nav-link-div {
  display: flex;
  gap: 15px;
  margin-right: 30px;
  margin-left: 10px;
}
.nav-link-div a {
  padding: 10px;
  transition: all .3s ease-in-out;
  cursor: pointer;
  color: transparent;
}
.nav-link-div a:hover {
  opacity: 1;
  scale: .9;
}
.githubBtn{
  background-image: url(
  '../assets/github-mark-white.svg');
  opacity: .8;
  background-size: cover;
  width: 30px;
  height: 30px;
}
.linkedinBtn{
  background-image: url(
  '../assets/linkedin-svgrepo-com.svg');
  opacity: .8;
  background-size: cover;
  width: 30px;
  height: 30px;
}
.stackoverflowBtn{
  background-image: url(
  '../assets/stack-overflow-svgrepo-com.svg');
  opacity: .8;
  background-size: cover;
  width: 30px;
  height: 30px;
}
@media screen and (max-width: 850px) {
  .nav-link-div {
    display: none;
  }
.navbar-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.middle-nav-div a {
  font-size: 15px;
}
.nav-header {
  margin: 0
}
.nav-link-div {
  margin: 0;    
}
}
@media screen and (max-width: 500px) {

  .middle-nav-div {
    gap:  15px;
    padding: 5px;
  }
  .middle-nav-div a {
    font-size: 13px;
    }
}